.elf--notification-view{display:inline-block;position:absolute;right:10px;top:10px;z-index:10000}.elf--notification-view .item{position:relative;min-width:300px;border-radius:4px;color:#fff;margin-bottom:10px;padding:10px 20px;opacity:1;transition:opacity 10s linear}.elf--notification-view .item .description{padding-top:10px}.elf--notification-view .item .icon{position:absolute;right:10px;top:10px;display:inline-block}.elf--notification-view .item .icon svg path{fill:currentColor}.elf--notification-view .item.alert{background-color:rgba(228,80,80,.87)}.elf--notification-view .item.alert .title{font-weight:bold;color:#fff}.elf--notification-view .item.alert .description{color:#fff}